.partners {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px 0;
    height: 177px;
    min-height: 177px;
    max-height: 177px;
    @media(max-width: 1200px) {
        height: 159px;
        min-height: 159px;
        max-height:159px;
    }
    &-slider {
        position: relative;
        padding: 0 36px;
        @media(max-width: 1200px) {
            display: none !important;
        }
        & .owl-dots {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            margin: 27px 0 0;
            & button {
                position: relative;
                width: 13px;
                height: 13px;
                outline: none !important;
                border: 1px solid #2756ED !important;
                background: transparent;
                border-radius: 50%;
                transition: 0.3s ease;
                &:not(:last-child) {margin-right: 10px;}
                & span {display: none;}
                &.active,
                &:hover {
                    background: #2756ED;
                }
            }
        }
        & .owl-nav {
            & button {
                position: absolute;
                top: calc(50% - 18px);
                display: block;
                width: 36px;
                height: 36px;
                background-repeat: no-repeat !important;
                background-position: center !important;
                background-size: contain !important;
                opacity: 0.2;
                transition: 0.3s ease;
                &:hover {opacity: 1;}
                & span {
                    display: none;
                }
                &.owl-prev {
                    left: 0;
                    background-image: url('../images/sl-arrow-prev.svg') !important;
                }
                &.owl-next {
                    right: 0;
                    background-image: url('../images/sl-arrow-next.svg') !important;
                }
            }
        }
    }
    & .btn {
        display: none;
        @media(max-width: 1200px) {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            max-width: 384px;
            margin: 0 auto;
        }
    }
    & .partner {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 44px;
        padding: 0 10px;
        opacity: 0.2;
        transition: 0.3s ease;
        &:hover {opacity: 1;}
        & img {
            width: auto !important;
            max-width: 100%;
            max-height: 44px;
        }
    }
}