.search {
    padding-bottom: 100px;
    & h1 {
        margin-bottom: 53px;
        font-family: 'Montserrat Regular';
        font-size: 25px;
        line-height: 30px;
        color: #303030;
        @media(max-width: 767px) {
            margin-bottom: 33px;
            font-size: 18px;
            line-height: 24px;
        }
        & span {
            font-family: 'Montserrat Bold';
        }
    }
    & .catalog {
        &-list {
            width: 100%;
            max-width: 100%;
        }
        &-item {
            width: calc((100% / 4) - 15px);
            @media(max-width: 767px) {
                width: 100%;
            }
        }
    }
}