







.popup-wrapper {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	padding: 15px;
	opacity: 0;
	z-index: 20;
	display: flex;
	align-items: center;
	justify-content: center;
    visibility: hidden;
    opacity: 0;
	transition: 0.5s ease;
}

.popup-wrapper.active {
    visibility: visible;
    opacity: 1;
}

.popup-layout {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	z-index: 6;
	display: flex;
	align-items: center;
	justify-content: center;
	background: rgba(0, 0, 0, 0.4);
}

.popup {
  position: relative;
  width: 100%;
  max-width: 366px;
  padding: 21px 50px;
  background: #FFFFFF;
  &-img {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 20px;
      width: 100%;
      & img {
          max-height: 68px;
      }
  }
  &-thanks {
    & h5 {
        display: block;
        margin-bottom: 16px;
        font-family: 'Montserrat SemiBold';
        font-size: 18px;
        line-height: 22px;
        text-align: center;
        color: #303030;
    }
    & a {
        display: block;
        font-family: 'Montserrat Regular';
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        text-decoration: underline;
        color: #303030;
        &:hover {
            text-decoration: none;
        }
    }
  }
}