.btn {
    display: inline-block;
    padding: 0 20px;
    min-width: 135px;
    background: #2756ED;
    border-radius: 4px;
    transition: 0.3s ease;
    &-wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 40px;
        font-family: 'Montserrat SemiBold';
        font-size: 14px;
        line-height: 40px;
        color: #ffffff;
        & span {white-space: nowrap;}
    }
    &-big {
        padding: 0 74px;
        & .btn-wrap {
            min-height: 50px;
            font-size: 16px;
            line-height: 50px;
        }
    }
    & img {
        max-width: 16px;
        margin-right: 10px;
    }
    &:hover {
        @media (min-width: 1100px) {
            background: #0e3fdf;
        }
    }
}