.banner {
    position: relative;
    & .header {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 5;
        width: 100%;
    }
    &-slider {
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        & .owl-dots {
            position: absolute;
            bottom: 64px;
            left: 0;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            height: 33px;
            width: 100%;
            @media(max-width: 1024px) {
                bottom: 13px;
            }
            @media(max-height: 1024px)  {
                bottom: 13px;
            }
            & button {
                position: relative;
                width: 20px;
                height: 21px;
                outline: none !important;
                border: none !important;
                transition: 0.3s ease;
                &::before {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    display: block;
                    width: 0.5px;
                    height: 100%;
                    background-color: rgba(255, 255, 255, 0.2);
                    transition: 0.3s ease;
                }
                &:not(:last-child) {margin-right: 5px;}
                & span {
                    position: absolute;
                    left: 50%;
                    min-width: 30px;
                    bottom: calc(100% + 13px);
                    transform: translateX(-50%);
                    font-family: 'Montserrat SemiBold';
                    font-size: 14px;
                    line-height: 14px;
                    text-align: center;
                    color: #FFFFFF;
                    opacity: 0;
                    transition: 0.3s ease;
                }
                &.active {
                    height: 33px;
                    &::before {
                        background-color: rgba(255, 255, 255, 1);
                    }
                    & span {opacity: 1;}
                }
            }
        }
    }
    &-item {
        position: relative;
        width: 100%;
        height: calc(100vh - 177px);
        padding: 165px 0 185px;
        @media(max-height: 1024px)  {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 165px 0 80px;
        }
        @media(max-width: 1200px) {
            height: auto !important;
        }
        @media(max-width: 1024px) {
            padding: 230px 0 85px !important;
        }
        & .wrapper {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media(max-width: 1024px) {
                flex-direction: column;
                align-items: center;
            }
        }
        &-cnt {
            padding-right: 10px;
            @media(max-width: 1024px) {
                width: 100%;
                padding: 0 0 20px;
            }
            & .title {
                margin-bottom: 30px;
                @media(max-width: 1024px) {
                    margin-bottom: 20px;
                }
                @media(max-height: 800px)  {
                    font-size: 41px;
                    line-height: 50px;
                }
            }
        }
        &-img {
            width: 100%;
            max-width: 535px;
            @media(max-width: 1200px) {
                max-width: 400px !important;
            }
            @media(max-height: 1024px)  {
                max-width: 390px;
            }
            @media(max-height: 800px)  {
                max-width: 250px;
            }
            & img {
                width: auto !important;
                max-width: 100%;
                @media(max-height: 1024px)  {
                    max-height: 390px;
                }
                @media(max-height: 800px)  {
                    max-height: 250px;
                }
            }
        }
        &-text {
            position: absolute;
            right: -503px;
            top: calc(50% - 40px);
            font-family: 'Montserrat Bold';
            font-size: 180px;
            line-height: 219px;
            color: #FFFFFF;
            opacity: 0.03;
            transform: rotate(-90deg) translateY(-50%);
        }
    }
}