.checkout {
    padding-bottom: 60px;
    &-row {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        @media(max-width: 1024px) {
            display: block;
        }
    }
    &-el {
        background: #FFFFFF;
        box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.05);
        border-radius: 4px;
        & h2 {
            margin-bottom: 20px;
            font-family: 'Montserrat SemiBold';
            font-size: 24px;
            line-height: 29px;
            color: #303030;
        }
    }
    &-data {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-flow: row wrap;
        width: 100%;
        max-width: 850px;
        margin-right: 20px;
        padding: 30px;
        @media(max-width: 1024px) {
            max-width: 100%;
            margin-right: 0px;
            margin-bottom: 20px;
            padding: 30px 20px;
        }
        & h2 {width: 100%;}
        & .form-group {
            width: calc(50% - 10px);
            @media(max-width: 1024px) {
                width: 100%;
            }
            &-textarea {width: 100%;}
        }
    }
    &-order {
        width: 100%;
        max-width: 270px;
        padding: 20px;
        @media(max-width: 1024px) {
            max-width: 100%;
        }
        &-row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;
            & span {
                &:first-child {
                    font-family: 'Montserrat Regular';
                    font-size: 16px;
                    line-height: 20px;
                    color: #9F9F9F;
                    & b {
                        font-family: 'Montserrat SemiBold';
                        font-size: 18px;
                        line-height: 22px;
                        color: #303030;
                    }
                }
                &:nth-child(2) {
                    font-family: 'Montserrat Medium';
                    font-size: 16px;
                    line-height: 20px;
                    color: #595959;
                    & b {
                        font-family: 'Montserrat Bold';
                        font-size: 24px;
                        line-height: 29px;
                        color: #303030;
                    }
                }
            }
        }
        &-bottom {
            @media(max-width: 1024px) {
                display: flex;
                flex-direction: column;
                align-content: center;
                justify-content: center;
            }
        }
        & .btn {
            width: 100%;
            margin-bottom: 20px;
            padding: 0 10px;
            @media(max-width: 1024px) {
                max-width: 320px;
                margin: 0 auto 20px;
            }
        }
    }
}

.checkbox-item {
    cursor: pointer;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    @media(max-width: 1024px) {
        max-width: 320px;
        margin: 0 auto;
    }
    &-trigger {
        &-wrap {
            &-cat {
                display: none;
                padding-left: 15px;
            }
            &.active {
                & .checkbox-item-trigger-wrap-cat {
                    display: block;
                }
            }
        }
    }
    & > input {
        display: none;
        &:checked + .checkbox-item-check {
            background-color: #2756ED;
            & img {
                visibility: visible;
                opacity: 1;
            }
        }
    }
    &-check {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 15px;
        height: 15px;
        min-width: 15px;
        margin-right: 11px;
        border-radius: 2px;
        border: 1px solid #2756ED;
        transition: 0.3s ease;
        & img {
            visibility: hidden;
            opacity: 0;
            transition: 0.3s ease;
        }
    }
    &-text {
        font-family: 'Montserrat Regular';
        font-size: 14px;
        line-height: 17px;
        color: #303030;
    }
}


.form {
    width: 100%;
    &-group {
        width: 100%;
        &:not(:last-child) {margin-bottom: 20px;}
        & input {
            display: block;
            width: 100%;
            height: 40px;
            padding: 5px 14px 5px;
            font-family: 'Montserrat Regular';
            font-size: 16px;
            line-height: 40px;
            color: #9F9F9F;
            background: #FFFFFF;
            border: 1px solid #D7D7D7;
            border-radius: 4px;
            transition: 0.3s ease;
            outline: none;
            @media(max-width: 400px) {
                font-size: 12px;
            }
            &::-webkit-input-placeholder {color: #9F9F9F;}
            &::-moz-placeholder {color: #9F9F9F;}
            &:-ms-input-placeholder {color: #9F9F9F;}
            &:-moz-placeholder {color: #9F9F9F;}
            &:focus,
            &:hover {
                border: 1px solid #2756ED; 
            }
        }
        & textarea {
            display: block;
            width: 100%;
            height: 134px;
            padding: 10px 14px;
            font-family: 'Montserrat Regular';
            font-size: 16px;
            line-height: 20px;
            color: #9F9F9F;
            background: #FFFFFF;
            border: 1px solid #D7D7D7;
            border-radius: 4px;
            transition: 0.3s ease;
            outline: none;
            @media(max-width: 400px) {
                font-size: 12px;
            }
            &::-webkit-input-placeholder {color: #9F9F9F;}
            &::-moz-placeholder {color: #9F9F9F;}
            &:-ms-input-placeholder {color: #9F9F9F;}
            &:-moz-placeholder {color: #9F9F9F;}
            &:focus,
            &:hover {
                border: 1px solid #2756ED; 
            }
        }
        &.has-error {
            & input,
            & textarea {
                background-color: rgba(242, 222, 222, 0.2);
                border: 1px solid #A50000;
                color: #A50000;
                &::-webkit-input-placeholder {color: #A50000;}
                &::-moz-placeholder {color: #A50000;}
                &:-ms-input-placeholder {color: #A50000;}
                &:-moz-placeholder {color: #A50000;} 
            }
        }
    }
}