html, body {
    height: 100%;
    &.hidden {overflow: hidden;}
}

.main {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    &-content {flex: 1 0 auto;}
    & footer {flex: 0 0 auto;}
}


.wrapper {
    width: 100%;
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
}

.section-block {
    padding: 70px 0 45px;
    @media (max-width: 1100px) {
        padding: 40px 0;
    }
}


// typo

.title {
    font-family: 'Montserrat Bold';
    font-size: 64px;
    line-height: 78px;
    color: #FFFFFF;
    @media(max-width: 1100px) {
        font-size: 48px;
        line-height: 56px;
    }
    @media(max-width: 1024px) {
        font-size: 41px;
        line-height: 50px;
    }
    @media(max-width: 380px) {
        font-size: 28px;
        line-height: 36px;
    }
    &-big {
        margin-bottom: 20px;
        font-family: 'Montserrat Bold';
        font-size: 35px;
        line-height: 43px;
        color: #303030;
    }
    &-2 {
        margin-bottom: 30px;
        font-family: 'Montserrat SemiBold';
        font-size: 24px;
        line-height: 29px;
        color: #303030;
    }
}


.search-form {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 560px;
    @media(max-width: 1100px) {
        max-width: 350px;
    }
    @media(max-width: 1024px) {
        max-width: 100%;
    }
    &-hidden {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        max-width: 425px;
        padding: 0 16px 16px;
        background-color: #FFFFFF;
        transition: 0.3s ease;
        opacity: 0;
        visibility: hidden;
        @media(max-width: 1024px) {
            max-width: 560px;
            left: 50%;
            transform: translateX(-50%);
        }
        & a {
            display: block;
            font-family: 'Montserrat Regular';
            font-size: 12px;
            line-height: 15px;
            color: #303030;
            transition: 0.3s ease;
            &:hover {color: #2756ED;}
            &:not(:last-child) {
                margin-bottom: 15px;
            }
            &:first-child {
                padding-top: 10px;
                border-top: 1px solid rgba(221, 221, 221, 0.27);
            }
        }
    }
    & input {
        display: block;
        width: 100%;
        max-width: 425px;
        min-height: 40px;
        height: 40px;
        padding: 3px 16px;
        outline: none;
        border: none;
        background-color: #FFFFFF;
        border-radius: 4px 0px 0px 4px;
        font-family: 'Montserrat Regular';
        font-size: 12px;
        line-height: 40px;
        color: #000000;
        transition: 0.3s ease;
        @media(max-width: 400px) {
            padding: 3px 10px;
        }
        &::-webkit-input-placeholder {color: #868686;}
        &::-moz-placeholder {color: #868686;}
        &:-ms-input-placeholder {color: #868686;}
        &:-moz-placeholder {color: #868686;}
        &:focus {
            border-radius: 4px 0 0 0;
        }
        &:focus  + .search-form-hidden {
            opacity: 1;
            visibility: visible;
        }
    }
    & .btn {
        border-radius: 0 4px 4px 0;
        @media(max-width: 400px) {
            min-width: 80px;
        }
    }
}

.breadcrumbs {
    padding: 0 0 10px;
    @media (max-width: 767px) {
        padding: 0 0 5px;
    }
    & .wrapper {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-flow: row wrap;
        & a,
        & span {
            display: inline-block;
            margin-bottom: 5px;
            font-family: 'Montserrat Regular';
            font-size: 14px;
            line-height: 17px;
            color: #303030;
        }
        & .slash {
            margin: 0 5px 5px;
        }
    }
}