.header {
    position: relative;
    padding: 23px 0 30px;
    &-top {
        padding-bottom: 28px;
        @media(max-width: 1200px) {
            display: none;
        }
        & .wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &-left {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            & a {
                font-family: 'Montserrat Regular';
                font-size: 14px;
                line-height: 14px;
                color: #ffffff;
                transition: 0.3s ease;
                &:hover {color: #2756ED;}
                &:not(:last-child) {margin-right: 55px;}
            }
        }
        &-right {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            & > a {
                font-family: 'Montserrat Bold';
                font-size: 14px;
                line-height: 14px;
                color: #ffffff;
                transition: 0.3s ease;
                &:hover {color: #2756ED;}
                &:not(:last-child) {margin-right: 39px;}
            }
        }
    }
    &-bottom {
        & .wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media(max-width: 1200px) {
                flex-flow: row wrap;
                & .search-form {
                    max-width: 100%;
                    order: 4;
                    margin-top: 30px;
                }
            }
        }
        & .catalog-menu-trigger {
            @media(max-width: 1200px) {
                display: none;
            }
        }
        & .logo {
            width: 100%;
            max-width: 164px;
            @media(max-width: 1200px) {
                order:1;
            }
            @media(max-width: 400px) {
                max-width: 130px;
            }
            & img {max-width: 100%;}
        }
        &-right {
            @media(max-width: 1200px) {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                order: 2;
            }
            & .menu-trigger-open {
                display: none;
                align-items: flex-end;
                justify-content: space-between;
                flex-direction: column;
                width: 25px;
                height: 18px;
                margin-left: 48px;            
                @media(max-width: 1200px) {
                    display: flex;
                }
                @media(max-width: 400px) {
                    margin-left: 20px; 
                }
                & span {
                    display: block;
                    width: 100%;
                    height: 2px;
                    border-radius: 10px;
                    background-color: #ffffff;
                }
            }
            & .menu-trigger-close {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 29px;
                height: 29px;
                background: #2756ED;
                border-radius: 50%;
                margin-left: 44px;       
                @media(max-width: 400px) {
                    margin-left: 16px;
                }
                & img {
                    width: auto;
                    max-width: 11px;
                }
            }


        }
        & .cart {
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            min-width: 112px;
            &-icon {
                position: relative;
                margin-right: 22px;
                margin-top: 7px;
                & svg {
                    & path {
                        fill: #ffffff;
                        transition: 0.3s ease;
                    }
                }
                &-num {
                    position: absolute;
                    top: -9px;
                    right: -9px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 18px;
                    height: 18px;
                    border-radius: 50%;
                    background: #2756ED;
                    font-family: 'Montserrat Bold';
                    font-size: 10px;
                    line-height: 10px;
                    color: #FFFFFF;
                    transition: 0.3s ease;
                    opacity: 0;
                }
            }
            &-info {
                display: block;
                visibility: hidden;
                transition: 0.3s ease;
                & span {
                    display: block;
                    font-size: 14px;
                    line-height: 17px;
                    color: #FFFFFF;
                    transition: 0.3s ease;
                }
                & span:nth-child(1) {
                    font-family: 'Montserrat SemiBold';
                }
                & span:nth-child(2) {
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    font-family: 'Montserrat Bold';
                    & .cart-info-sum {margin-right: 2px;}
                }
            }
            &.added {
                & .cart-icon-num {opacity: 1;}
                & .cart-info {visibility: visible;}
            }
        }
    }
    &-menu {
        display: none;
        @media(max-width: 1200px) {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: auto;
            z-index: 10;
            display: block;
            visibility: hidden;
            opacity: 0;
            padding: 23px 0 40px;
            background-color: #ffffff;
            transition: 0.3s ease;
            box-shadow: 0 0 10px #9e9e9e;
        }
        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            & .cart-icon svg path {fill: #303030;}
            & .cart-info span {color: #303030;}
        }
        &-links {
            width: 100%;
            padding-top: 60px;
            @media(max-height: 430px) {
                padding-top: 15px;
            }
            & .flex-container-center {
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 auto 25px;
                & .btn {
                    display: inline-block !important;
                }
                @media(max-height: 430px) {
                    margin: 0 auto 10px;
                }
            }
            &-top {
                & a {
                    display: block;
                    margin-bottom: 25px;
                    font-family: 'Montserrat Regular';
                    font-size: 18px;
                    line-height: 22px;
                    text-align: center;
                    color: #303030;
                    @media(max-height: 430px) {
                        margin-bottom: 10px;
                    }
                }
            }
            &-bottom {
                & a {
                    display: block;
                    font-family: 'Montserrat Bold';
                    font-size: 14px;
                    line-height: 17px;
                    text-align: center;
                    color: #303030;
                    &:not(:last-child) {
                        margin-bottom: 15px;
                        @media(max-height: 430px) {
                            margin-bottom: 10px;
                        }
                    }
                }
            }
        }
    }
    &-white {
        margin-bottom: 58px;
        @media(max-width: 1200px) {
            margin-bottom: 25px;
        }
        & .header-top {
            &-right,
            &-left {
                & a {
                    color: #303030;
                }
            }
        }
        & .search-form  {
            & input {
                border: 1px solid #D7D7D7;
                border-right: 0;
                &:focus {
                    border-radius: 4px 0 0 0;
                    border-bottom: 1px solid transparent;
                }
            }
            &-hidden {
                box-shadow: none;
                border: 1px solid #D7D7D7;
                border-top: 0;
            }
        }
        & .cart {
            &-icon {
                & svg {
                    & path {
                        fill: #303030;
                    }
                }
            }
            &-info {
                & span {
                    color: #303030;
                }
            }
        }
        & .menu-trigger-open {
            & span {
                background-color: #2756ED;
            }
        }
    }
    &-categories {
        position: absolute;
        z-index: 1;
        top: calc(100% - 35px);
        left: 0;
        width: 100%;
        visibility: hidden;
        opacity: 0;
        transition: 0.3s ease;
        @media(max-width: 1200px) {
            top: 0;
            height: 100vh;
            z-index: 20;
            padding: 20px 0 80px;
            background-color: #ffffff;
        }
        &-close {
            &-wrap {
                display: none;
                @media(max-width: 1200px) {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-bottom: 40px !important;
                    width: 100%;
                    max-width: 300px;
                    margin: 0 auto;
                }
                @media(max-width: 500px) {
                    max-width: 100%;
                }
                & p {
                    font-family: 'Montserrat Bold';
                    font-size: 16px;
                    line-height: 16px;
                    text-transform: uppercase;
                    color: #303030;
                }
            }
            @media(max-width: 1200px) {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 29px;
                height: 29px;
                background: #2756ED;
                border-radius: 50%;
            }
        }
        &-items {
            position: relative;
            width: 100%;
            padding: 16px 5px 25px 20px;
            border-radius: 4px;
            overflow: hidden;
            transition: 0.3s ease;
            &:hover {
                @media(min-width: 1201px)  {
                    box-shadow: 0px 5px 25px rgba(5, 5, 5, 0.13);
                }
            }
            @media(max-width: 1200px) {
                width: 100%;
                max-width: 300px;
                margin: 0 auto;
                padding: 0;
                height: calc(100vh - 89px);
                border-radius: 0;
                box-shadow:none;
                overflow: scroll;
            }
            @media(max-width: 500px) {
                max-width: 100%;
            }
            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
                display: block;
                width: 100%;
                max-width: 194px;
                height: 100%;
                background-color: #ffffff;
                box-shadow: 0px 5px 25px rgba(5, 5, 5, 0.13);
                @media(max-width: 1200px) {
                    display: none;
                }
            }
            & .header-categories-item > a {
                position: relative;
                z-index: 2;
                @media(max-width: 1200px) {
                    position: static;
                }
            }
        }
        &-item {
            width: 100%;
            max-width: 174px;
            @media(max-width: 1200px) {
                max-width: 100%;
            }
            & > a {
                display: flex;
                align-items: center;
                justify-content: space-between;
                width: 100%;
                max-width: 174px;
                padding: 9px 0;
                font-family: 'Montserrat SemiBold';
                font-size: 14px;
                line-height: 14px;
                color: #303030;
                transition: 0.3s ease;
                @media(max-width: 1200px) {
                    max-width: 100%;
                }
                & svg {
                    display: none;
                    width: 10px;
                    min-width: 10px;
                    height: 5px;
                    margin-left: 10px;
                    margin-right: 9px;
                    transition: 0.3s ease;
                    @media(max-width: 1200px) {
                        display: block;
                    }
                    & path {
                        fill: #303030;
                        transition: 0.3s ease;
                    }
                }
                
                @media(min-width: 1201px) {
                    &:hover {color: #2756ED;}
                }
                
            }
            &-hidden {
                position: absolute;
                right: 0;
                top: 0;
                display: flex;
                align-items: flex-start;
                justify-content: flex-start;
                flex-flow: column wrap;
                width: calc(100% - 194px);
                height: 100%;
                padding: 25px;
                visibility: hidden;
                opacity: 0;
                background-color: #ffffff;
                @media(max-width: 1200px) {
                    position: static;
                    width: 100%;
                    padding: 15px 0;
                    visibility: visible;
                    opacity: 1;
                    display: none;
                }
                & a {
                    display: block;
                    width: 100%;
                    max-width: 159px;
                    padding: 0 5px 0 0;
                    font-family: 'Montserrat Regular';
                    font-size: 13px;
                    line-height: 13px;
                    color: #303030;
                    transition: 0.3s ease;
                    @media(max-width: 1200px) {
                        max-width: 100%;
                        padding: 0;
                    }
                    &:not(:last-child) {margin-bottom: 16px;}
                    &:hover {color: #2756ED;}
                }
                & p {
                    display: block;
                    width: 100%;
                    max-width: 159px;
                    padding: 0 5px 0 0;
                    font-family: 'Montserrat Bold';
                    font-size: 13px;
                    line-height: 13px;
                    color: #303030;
                    @media(max-width: 1200px) {
                        max-width: 100%;
                        padding: 0;
                    }
                    &:not(:last-child) {margin-bottom: 16px;}
                }
            }
            &:hover {
                @media(min-width: 1201px) {
                    max-width: 100%;
                    & .header-categories-item-hidden {
                        visibility: visible;
                        opacity: 1;
                    }
                    & > a {color: #2756ED;}
                }
            }
            &.active {
                @media(max-width: 1201px) {
                    & .header-categories-item-hidden {
                        display: block;
                    }
                    & > a {
                        color: #2756ED;
                        & svg {
                            transform: rotate(180deg);
                            & path {
                                fill: #2756ED;
                            }
                        }
                    }
                }
            }
        }
    }
    &.open-cat {
        & .header-categories {
            opacity: 1;
            visibility: visible;
        }
    }
    &.open {
        & .header-bottom {
            &-right {
                & .menu-trigger-open {
                    & span {
                        background-color: #2756ED;
                    }
                }
            }
        }
        & .header-bottom.header-menu {
            opacity: 1;
            visibility: visible;
        }
    }
}


.lang-links {
    display: flex;
    align-items:  center;
    justify-content: center;
    margin-right: 67px;
    @media(max-width: 1200px) {
        margin: 20px 0 0;
    }
    & span {
        display: inline-block;
        font-family: 'Montserrat Regular';
        font-size: 14px;
        line-height: 17px;
        color: #9D9D9D;
        &:first-child {margin-right: 11px;}
    }
    & a {
        display: inline-block;
        font-family: 'Montserrat Regular';
        font-size: 14px;
        line-height: 17px;
        color: #fff;
        text-decoration: underline;
        transition: 0.3s ease;
        @media(max-width: 1200px) {
            color: #303030;
        }
        &:first-child {margin-right: 11px;}
        &:hover {text-decoration: none;}
    }
}