.catalog {
    padding-bottom: 100px;
    &-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;
        @media(max-width: 767px) {
            display: block;
            margin-bottom: 30px;
        }
        & h1 {
            font-family: 'Montserrat Bold';
            font-size: 35px;
            line-height: 43px;
            color: #303030;
            @media(max-width: 767px) {
                margin-bottom: 30px;
            }
        }
    }
    &-row {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        @media(max-width: 767px) {
            display: block;
        }
    }
    &-filter {
        position: relative;
        width: 100%;
        max-width: 270px;
        padding: 20px;
        background: #FFFFFF;
        box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.05);
        border-radius: 4px;
        @media(max-width: 767px)  {
            max-width: 100%;
            padding: 0;
            margin-bottom: 50px;
            border-radius: 4px;
            overflow: hidden;
            &   .checkbox-item {max-width: 100%;}
        }
        &-trigger {
            display: none;
            @media(max-width: 767px) {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px 20px;
                background: #FFFFFF;
                border-radius: 4px;
                & span {
                    font-family: 'Montserrat SemiBold';
                    font-size: 18px;
                    line-height: 18px;
                    color: #303030;
                }
                & img {
                    transition: 0.3s ease;
                }
            }
        }
        &.open {
            & .catalog-filter-trigger img {transform: rotate(180deg);}
            & .catalog-filter-cnt {display: block;}
        }
        &-cnt {
            @media(max-width: 767px) {
                display: none;
                z-index: 2;
                width: 100%;
                background-color: #FFFFFF;
                padding: 30px 20px 30px;
            }
        }
        & h4 {
            margin-bottom: 15px;
            font-family: 'Montserrat SemiBold';
            font-size: 18px;
            line-height: 22px;
            color: #303030;
        }
        & h5 {
            margin-bottom: 11px;
            font-family: 'Montserrat SemiBold';
            font-size: 16px;
            line-height: 20px;
            color: #303030;
        }
        &-links {
            &-row {
                & a {
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    margin-bottom: 10px;
                    font-family: 'Montserrat Regular';
                    font-size: 14px;
                    line-height: 17px;
                    color: #303030;
                    transition: 0.3s ease;
                    &:hover {color: #2756ED;}
                    & img {margin-right: 14px;}
                }
                & .catalog-filter-links-row {
                    padding-left: 12px;
                }
            }
        }
    }
    &-list {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-flow: row wrap;
        width: 100%;
        max-width: 850px;
        margin-left: 20px;
        @media(max-width: 992px) {
            max-width: 100%;
            margin-left: 0;
        }
        &-2 {
            @media(max-width: 767px) {
                .catalog-item {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: flex-start;
                    width: calc(50% - 10px);
                    padding: 16px 15px;
                    &-img {width: 100%;}
                    &-info {width: 100%;}
                    &-title-new {
                        display: block;
                        margin-top: 0;
                    }
                }
            }
        }
    }
    &-item {
        position: relative;
        padding: 44px 17px 19px;
        width: calc((100% / 3) - 13px);
        margin-bottom: 20px;
        background: #FFFFFF;
        box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.03);
        border-radius: 4px;
        transition: 0.3s ease;
        @media(max-width: 992px) {
            width: calc((100% / 2) - 13px);
        }
        @media(max-width: 767px) {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            padding: 20px 15px 21px;
        }
        &:hover {
            box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.15);
        }
        &-info {
            @media(max-width: 767px) {
                width: calc((100% / 2) - 10px);
            }
        }
        &-type {
            position: absolute;
            z-index: 0;
            top: 20px;
            left: 17px;
            font-family: 'Montserrat SemiBold';
            font-size: 12px;
            line-height: 12px;
            color: #5EC444;
            &-red {
                color: #E7340C;
            }
        }
        &-img {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 200px;
            max-height: 200px;
            overflow: hidden;
            margin-bottom: 25px;
            @media(max-width: 767px) {
                width: calc((100% / 2) - 10px);
                height: 126px;
                max-height: 126px;
            }
            & img {
                max-width: 100%;
                max-height: 200px;
                @media(max-width: 767px) {
                    max-height: 126px;
                }
            }
        }
        &-category {
            margin-bottom: 10px;
            font-family: 'Montserrat SemiBold';
            font-size: 12px;
            line-height: 15px;
            color: #DADADA;
        }
        &-title {
            margin-bottom: 8px;
            font-family: 'Montserrat SemiBold';
            font-size: 16px;
            line-height: 20px;
            color: #303030;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            overflow: hidden;
            height: 40px;
            -webkit-box-orient: vertical;
            transition: 0.3s ease;
            @media(max-width: 767px) {
                font-size: 12px;
                line-height: 16px;
                height: 32px;
            }
            &-new {
                width: 100%;
                margin-top: 37px;
                font-family: 'Montserrat SemiBold';
                font-size: 16px;
                line-height: 20px;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                overflow: hidden;
                height: 40px;
                text-align: center;
                -webkit-box-orient: vertical;
                color: #1A1A1A;
                @media(max-width: 767px) {
                    font-size: 12px;
                    line-height: 16px;
                    height: 32px;
                }
            }
            &:hover {
                color: #2756ED;
            }
        }
        &-art {
            margin-bottom: 8px;
            font-family: 'Montserrat Regular';
            font-size: 14px;
            line-height: 17px;
            color: #B7B7B7;
            @media(max-width: 767px) {
                font-size: 10px;
                line-height: 10px;
            }
        }
        &-price {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            margin-bottom: 8px;
            font-family: 'Montserrat SemiBold';
            font-size: 30px;
            line-height: 37px;
            color: #303030;
            @media(max-width: 1100px) {
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;
                min-height: 59px;
            }
            @media(max-width: 767px) {
                font-size: 20px;
                line-height: 30px;
                min-height: 0;
            }
            &-last {
                display: inline-block;
                margin-left: 15px;
                font-family: 'Montserrat Regular';
                font-size: 14px;
                line-height: 17px;
                text-decoration: line-through;
                color: #4F4F4F;
                @media(max-width: 1100px) {
                    margin: 5px 0 0;
                }
            }
        }
        & .btn {
            width: 100%;
            max-width: 156px;
            padding: 0 15px;
        }
    }
}

.select-block {
    position: relative;
    &-trigger {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        &-text {
            & div {
                font-size: 14px;
                line-height: 14px;
                color: #303030;
            }
            &-1 {
                margin-right: 5px;
                font-family: 'Montserrat Regular';
            }
            &-2 {
                font-family: 'Montserrat SemiBold';
            }
        }
        & img  {
            display: block;
            margin-left: 14px;
            transition: 0.3s ease;
        }
    }
    &-hidden {
        position: absolute;
        z-index: 3;
        top: calc(100% + 10px);
        left: 0;
        width: 100%;
        padding: 15px 7px;
        border-radius: 4px;
        background-color: #ffff;
        opacity: 0;
        visibility: hidden;
        transition: 0.3s ease;
        box-shadow: 0px 5px 25px rgba(5, 5, 5, 0.13);
        &-item {
            display: block;
            cursor: pointer;
            font-family: 'Montserrat Regular';
            font-size: 14px;
            line-height: 14px;
            color: #303030;
            transition: 0.3s ease;
            &.active,
            &:hover {
                color: #2756ED;
            }
            &:not(:last-child) {margin-bottom: 15px;}
        }
    }
    &.open {
        & .select-block-hidden {
            opacity: 1;
            visibility: visible;
        }
        & .select-block-trigger img {transform: rotate(180deg);}
    }
}