.card {
    padding-bottom: 70px;
    &-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: calc(100% - 97px);
        margin-bottom: 58px;
        padding: 34px 45px;
        background: #FFFFFF;
        box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.05);
        border-radius: 4px;
        @media(max-width: 992px) {
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            width: 100%;
            max-width: 100%;
            padding: 30px 17px;
        }
        &-wrap {
            position: relative;
            display: flex;
            align-items: flex-start;
            justify-content: flex-end;
            @media(max-width: 992px) {
                justify-content: center;
            }
        }
        &-slider {
            width: 100%;
            max-width: 470px;
            @media(max-width: 992px) {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                flex-direction: column;
                margin-bottom: 34px;
                max-width: 100%;
                order: 1;
            }
            &-right {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                max-width: 470px;
                min-height: 300px;
                padding-right: 10px;
                @media(max-width: 992px) {
                    min-height: 300px;
                    margin-bottom: 30px;
                    max-width: 767px;
                    padding-right: 0;
                    order: 1;
                }
                @media(max-width: 400px) {
                    min-height: 280px;
                }
                &-top {
                    position: absolute;
                    z-index: 0;
                    top: 0;
                    left: 0;
                    font-family: 'Montserrat SemiBold';
                    font-size: 12px;
                    line-height: 12px;
                    color: #5EC444;
                    &-red {
                        color: #E7340C;
                    }
                }
                & img {
                    max-width: 100%;
                    max-height: 368px;
                    @media(max-width: 992px) {
                        max-height: 300px;
                    }
                    @media(max-width: 400px) {
                        max-height: 280px;
                    }
                }
            }
            &-left {
                position: absolute;
                top: -20px;
                left: -20px;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: flex-start;
                width: 117px;
                height: 408px;
                padding: 20px;
                overflow-y: scroll;
                @media(max-width: 992px) {
                    position: static;
                    flex-flow: row nowrap;
                    width: 100%;
                    height: 97px;
                    padding: 0 !important;
                    overflow-y: hidden;
                    overflow-x: scroll;
                    order: 2;
                }
                &::-webkit-scrollbar {
                    width: 0;
                }
                   
                &::-webkit-scrollbar-track {
                    box-shadow: none;
                }
                   
                &::-webkit-scrollbar-thumb {
                    outline: 0px solid slategrey;
                }
                & a {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    height: 77px;
                    min-height: 77px;
                    padding: 10px 16px;
                    background-color: #FFFFFF;
                    background: #FFFFFF;
                    box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.05);
                    border-radius: 4px !important;
                    overflow: hidden;
                    border: 2px solid #FFFFFF;
                    transition: 0.3s ease;
                    @media(max-width: 992px) {
                        margin: 0 !important;
                        width: calc((100% / 4));
                        min-width: calc((100% / 4));
                        box-shadow: none;
                    }
                    @media(max-width: 400px) {
                        width: calc((100% / 3));
                        min-width: calc((100% / 3));
                    }
                    &:hover,
                    &.active {border: 2px solid #2756ED;}
                    &:not(:last-child) {margin-bottom: 20px;}
                    & img {
                        max-width: 100%;
                        max-height: 57px;
                    }
                }
            }
        }
        &-info {
            width: 100%;
            max-width: 396px;
            order: 2;
            @media(max-width: 992px) {
                max-width: 767px;
            }
            & h1 {
                margin-bottom: 29px;
                font-family: 'Montserrat Regular';
                font-size: 35px;
                line-height: 43px;
                color: #303030;
            }
            &-row {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 19px;
                @media(max-width: 400px) {
                    flex-direction: column;
                    align-items: flex-start;
                }
                & p {
                    font-family: 'Montserrat Regular';
                    font-size: 16px;
                    line-height: 20px;
                    &:first-child {
                        color: #000000;
                        @media(max-width: 400px) {
                            order: 2;
                        }
                    }
                    &:last-child {
                        color: #B7B7B7;
                        @media(max-width: 400px) {
                            order: 1;
                            margin-bottom: 20px;
                        }
                    }
                }
            }
            &-price {
                & div {
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    margin-bottom: 8px;
                    & p {
                        margin-right: 9px;
                        font-family: 'Montserrat Regular';
                        font-size: 14px;
                        line-height: 14px;
                        text-decoration: line-through;
                        color: #4F4F4F;
                    }
                    & span {
                        display: inline-block;
                        padding: 4px 10px;
                        font-family: 'Montserrat Regular';
                        font-size: 10px;
                        line-height: 10px;
                        color: #FFFFFF;
                        background: #2756ED;
                        border-radius: 5px;
                    }
                }
                & > p {
                    margin-bottom: 20px;
                    font-family: 'Montserrat SemiBold';
                    font-size: 35px;
                    line-height: 43px;
                    color: #000000;
                }
                & .btn {
                    width: 100%;
                    max-width: 156px;
                }
            }
        }
    }
    &-info {
        width: 100%;
        margin-bottom: 30px;
        padding: 30px;
        background: #FFFFFF;
        box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.05);
        border-radius: 4px;
        @media(max-width: 992px) {
            padding: 30px 17px;
        }
        &-row {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            @media(max-width: 992px) {
                flex-direction: column;
                align-items: center;
                justify-content: flex-start;
            }
        }
        &-col {
            width: calc((100% / 3) - 10px);
            @media(max-width: 992px) {
                width: 100%;
                &:not(:last-child) {margin-bottom: 30px;}
            }
            & h4 {
                margin-bottom: 20px;
                font-family: 'Montserrat SemiBold';
                font-size: 18px;
                line-height: 22px;
                color: #303030;
                &:not(:first-child) {padding-top: 15px;}
            }
            & h5 {
                margin-bottom: 7px;
                font-family: 'Montserrat Regular';
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;
                color: #9F9F9F;
            }
            & p {
                font-family: 'Montserrat SemiBold';
                font-size: 16px;
                line-height: 20px;
                color: #595959;
                &:not(:last-child) {margin-bottom: 15px;}
            }
        }
    }
    &-benefits {
        display: flex;
        justify-content: space-between;
        @media(max-width: 767px)  {
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
        }
        & .card-bnf {
            width: calc((100% / 3) - 20px);
            margin-bottom: 30px;
            padding: 32px 30px 15px;
            background: #FFFFFF;
            box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.05);
            border-radius: 4px;
            @media(max-width: 992px) {
                padding: 30px 17px;
            }
            @media(max-width: 767px) {
                width: 100%;
            }
            &-top {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 15px;
                & h4 {
                    font-family: 'Montserrat SemiBold';
                    font-size: 24px;
                    line-height: 29px;
                    color: #303030;
                }
                & img {
                    min-width: 22px;
                    max-width: 32px;
                }
            }
            & p {
                font-family: 'Montserrat Regular';
                font-size: 16px;
                line-height: 26px;
                color: #595959;
            }
        }
    }
    &-text {
        width: 100%;
        padding: 30px;
        background: #FFFFFF;
        box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.05);
        border-radius: 4px;
        &:not(:last-child) {margin-bottom: 30px;}
        @media(max-width: 992px) {
            padding: 30px 17px;
        }
        & p {
            font-family: 'Montserrat Medium';
            font-size: 16px;
            line-height: 26px;
            color: #595959;
            &:not(:last-child) {margin-bottom: 26px;}
        }
    }
    &-tech {
        width: 100%;
        padding: 30px;
        background: #FFFFFF;
        box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.05);
        border-radius: 4px;
        &:not(:last-child) {margin-bottom: 30px;}
        @media(max-width: 992px) {
            padding: 30px 17px;
        }
        &-row {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            width: 100%;
            padding: 20px 51px 0 0;
            border-top: 1px solid #F0F0F0;
            @media(max-width: 992px) {
                display: block;
            }
            &:not(:last-child) {margin-bottom: 20px;}
            & p {
                width: 100%;
                max-width: 651px;
                margin-left: 15px;
                font-family: 'Montserrat Regular';
                font-size: 14px;
                line-height: 24px;
                color: #151515;
                @media(max-width: 992px) {
                    max-width: 100%;
                    margin: 0 0 15px;
                }
            }
            & h4 {
                width: 100%;
                max-width: 363px;
                font-family: 'Montserrat SemiBold';
                font-size: 14px;
                line-height: 24px;
                color: #151515;
                @media(max-width: 992px) {
                    max-width: 100%;
                }
            }
        }
    }
    &-comparison {
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding: 30px;
        background: #FFFFFF;
        box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.05);
        border-radius: 4px;
        &:not(:last-child) {margin-bottom: 30px;}
        @media(max-width: 992px) {
            flex-direction: column;
            align-items: center;
            padding: 30px 17px;
        }
        &-column {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            width: calc((100% - 265px) / 4);
            @media(max-width: 992px) {
                width: 100%;
                max-width: 100%;
            }
            &-1 {
                align-items: flex-start;
                width: 100%;
                max-width: 265px;
                @media(max-width: 992px) {
                    width: 100%;
                    max-width: 100%;
                    margin-bottom: 20px;
                }
                & .card-comparison-row {
                    padding: 0 5px 0 0;
                    @media(max-width: 992px) {
                        padding: 0;
                        border-top: none !important;
                       &:not(:first-child) {display: none;}
                    }
                    & h4 {
                        text-align: left;
                    }
                }
            }
            &:nth-child(2) {
                & .card-comparison-row:first-child {
                    @media(max-width: 992px) {
                        padding: 0 0 20px;
                        border-top: none !important;
                    }
                }
            }
            &:not(.card-comparison-column-1) {
                & h4 {
                    display: none;
                    @media(max-width: 992px) {
                        display: block;
                        margin-bottom: 15px;
                        text-align: left;
                    }
                }
            }
        }
        &-row {
            display: flex;
            align-items: center;
            justify-content: center;
            min-height: 61px;
            width: 100%;
            padding: 5px 5px;
            border-top: 2px solid #F0F0F0;
            @media(max-width: 992px) {
                display: block;
                min-height: 0;
                padding: 20px 0;
                & h2,
                & p {
                    text-align: left !important;
                }
            }
            &:first-child {
                min-height: 106px;
                border-top: none;
                @media(max-width: 992px) {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    min-height: 0;
                    border-top: 2px solid #F0F0F0;
                }
                & h2 {
                    width: 100%;
                    text-align: left;
                }
                
            }
            & .title-2 {
                margin-bottom: 0;
                @media(max-width: 992px) {
                   width: 100%;
                   & br {display: none;}
                }
            }
            & a {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                max-width: 74px;
                min-height: 82px;
                & img {
                    max-width: 74px;
                    max-height: 82px;
                }
            }
            & h4 {
                width: 100%;
                font-family: 'Montserrat SemiBold';
                font-size: 14px;
                line-height: 17px;
                text-align: center;
                color: #151515;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                overflow: hidden;
                -webkit-box-orient: vertical;
            }
            & p {
                width: 100%;
                font-family: 'Montserrat Regular';
                font-size: 14px;
                line-height: 17px;
                text-align: center;
                color: #151515;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                overflow: hidden;
                -webkit-box-orient: vertical;
            }
            &-last {
                align-items: flex-start;
                padding-top: 20px !important;
                & p {
                    display: -webkit-box;
                    -webkit-line-clamp: 4;
                    overflow: hidden;
                    -webkit-box-orient: vertical;
                }
            }
        }
    }
}