.footer {
    padding: 19px 0;
    background-color: #363636;
    &-content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media(max-width: 1024px) {
            align-items: flex-end;
            width: 100%;
            max-width: 384px;
            margin: 0 auto;
        }
        @media(max-width: 380px) {
            flex-direction: column;
            align-items: center;
        }
    }
    &-left {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        @media(max-width: 1024px) {
            flex-flow: row wrap;
            width: 100%;
            max-width: 186px;
        }
        @media(max-width: 380px) {
            flex-direction: column;
            align-items: center;
            margin-bottom: 20px;
        }
        & a {
            font-family: 'Montserrat Regular';
            font-size: 14px;
            line-height: 14px;
            color: #ffffff;
            transition: 0.3s ease;
            &.logo {
                @media(max-width: 1024px) {
                    margin-bottom: 25px;
                }
            }
            &:hover {color: #2756ED;}
            &:not(:last-child) {
                margin-right: 49px;
                @media(max-width: 380px) {
                    margin: 0 0 15px;
                }
            }
        }
    }
    &-right {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        @media(max-width: 1024px) {
            flex-direction: column;
            align-items: flex-end;
            justify-content: flex-start;
        }
        & a {
            font-family: 'Montserrat Bold';
            font-size: 14px;
            line-height: 14px;
            color: #ffffff;
            transition: 0.3s ease;
            &:hover {color: #2756ED;}
            &:not(:last-child) {
                margin-right: 42px;
                @media(max-width: 1024px) {
                    margin:  0 0 10px;
                }
            }
        }
    }
}