.contacts {
    padding-bottom: 120px;
    & iframe {
        width: 100%;
    }
    &-info {
        padding: 16px 30px 9px;
        background: #FFFFFF;
        box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.03);
        border-radius: 4px;
        margin-bottom: 42px;
        @media(max-width: 767px) {
            padding: 20px 15px 9px;
        }
        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 14px;
            @media(max-width: 992px) {
                flex-flow: row wrap;
            }
            & a {
                display: block;
                margin-right: 20px;
                font-family: 'Montserrat SemiBold';
                font-size: 35px;
                line-height: 43px;
                color: #303030;
                transition: 0.3s ease;
                @media(max-width: 992px) {
                    width: 100%;
                    max-width: 100%;
                    margin-right: 0;
                    margin-bottom: 21px;
                    font-size: 30px;
                    line-height: 37px;
                }
                @media(max-width: 767px) {
                    font-size: 20px;
                    line-height: 27px;
                }
                &:hover {
                    color: #2756ED;
                }
            }
            & div {
                width: 100%;
                max-width: 468px;
                @media(max-width: 1150px) {
                    max-width: 250px;
                }
                @media(max-width: 992px) {
                    max-width: 60%;
                }
                @media(max-width: 767px) {
                    max-width: 100%;
                    margin-bottom: 30px;
                }
                & p {
                    font-family: 'Montserrat Medium';
                    font-size: 14px;
                    line-height: 26px;
                    color: #9F9F9F;
                }
            }
            &-img {
                display: flex !important;
                align-items: center;
                justify-content: flex-end;
                width: 100%;
                max-width: 195px;
                margin-right: 0;
                margin: 0 0 0 !important;
                @media(max-width: 992px) {
                    max-width: 40% !important;
                }
                @media(max-width: 767px) {
                    justify-content: center;
                    max-width: 100% !important;
                }
                & img {max-width: 100%;}
            }
        }
        &-row {
            & > p {
                margin-bottom: 20px;
                font-family: 'Montserrat Regular';
                font-size: 18px;
                line-height: 26px;
                color: #303030;
            }
            &-list {
                display: flex;
                align-items: flex-start;
                justify-content: flex-start;
                flex-flow: row wrap;
                width: 100%;
                max-width: 470px;
            }
        }
        &-col {
            width: 100%;
            max-width: 235px;
            margin-bottom: 21px;
            padding-right: 5px;
            @media(max-width: 992px) {
                max-width: 100%;
            }
            & p {
                margin-bottom: 10px;
                font-family: 'Montserrat SemiBold';
                font-size: 16px;
                line-height: 26px;
                color: #303030;
            }
            & a {
                font-family: 'Montserrat Regular';
                font-size: 16px;
                line-height: 26px;
                color: #303030; 
                transition: 0.3s ease;
                &:hover {
                    color: #2756ED;
                }
            }
        }
    }
}