.cart {
    &-list {
        & .no-more-content {
            display: none;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            width: 100%;
            & p {
                width: 100%;
                margin-bottom: 15px;
                font-family: 'Montserrat Bold';
                font-size: 25px;
                line-height: 25px;
                text-align: center;
                color: #303030;
            }
        }
        &.no-cnt {
            & .cart-section-row-top {display: none !important;}
            & .no-more-content {display: flex;}
        }
    }
    &-section {
        padding-bottom: 120px;
        &-flex {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            @media(max-width: 1024px) {
                display: block;
            }
            & .checkout-el {
                margin-top: 62px;
            }
        }
        &-content {
            width: 100%;
            max-width: 850px;
            margin-right: 20px;
            @media(max-width: 1024px) {
                max-width: 100%;
                margin: 0 0 20px;
            }
        }
        &-row {
            position: relative;
            display: flex;
            align-content: center;
            justify-content: space-between;
            padding: 0 0 20px;
            @media(max-width: 767px) {
                flex-flow: row wrap;
            }
            &-delete {
                cursor: pointer;
                position: absolute;
                top: 20px;
                right: 20px;
                transition: 0.3s ease;
                &:hover {opacity: 0.7;}
            }
            &:not(.cart-section-row-top) {
                padding: 27px 0;
                background: #FFFFFF;
                box-shadow: 0px 2px 20px rgba(48, 48, 48, 0.05);
                border-radius: 4px;
            }
            &:not(:last-child) {margin-bottom: 20px;}
            &.cart-section-row-top {
                align-items: flex-end;
                margin-bottom: 0;
                @media(max-width: 767px) {
                    padding-bottom: 0;
                    & .cart-section-col:not(.cart-section-col-1) {
                        display: none;
                    }   
                }
                & p {
                    line-height: 28px;
                }
                & .cart-section-col-1 {
                    justify-content: flex-start;
                    padding: 0 5px 0 0;
                    @media(max-width: 767px) {
                        width: 100%;
                    }
                    & h1 {
                        font-family: 'Montserrat Bold';
                        font-size: 35px;
                        line-height: 43px;
                        text-align: left;
                        color: #303030;
                        @media(max-width: 1180px) {
                            font-size: 16px;
                            line-height: 28px;
                        }
                        @media(max-width: 767px) {
                            font-size: 35px;
                            line-height: 43px;
                        }
                    }
                }
                & .cart-section-col-2 {
                    & p {text-align: left;}
                }
            }
        }
        &-col {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 0 10px;
            width: calc(100% / 5);
            @media(max-width: 767px) {
                width: 50%;
            }
            & p  {
                font-family: 'Montserrat SemiBold';
                font-size: 16px;
                line-height: 20px;
                text-align: center;
                color: #9F9F9F;
                & span {
                    display: none;
                    @media(max-width: 767px) {
                        display: inline-block;
                        margin-right: 22px;
                    }
                    @media(max-width: 400px) {
                        display: none;
                    }
                }
            }
            & a,
            & p > b {
                font-family: 'Montserrat SemiBold';
                font-weight: 600;
                font-size: 18px;
                line-height: 22px;
                color: #303030;
                transition: 0.3s ease;
                &:hover {color: #2756ED;}
            }
            &-1 {
                justify-content: flex-start;
                padding: 0 30px;
                text-align: left;
                @media(max-width: 767px) {
                    justify-content: center;
                    margin-bottom: 48px;
                }
            }
            &-2 {
                justify-content: flex-start;
                @media(max-width: 767px) {
                    margin-bottom: 48px;
                }
                & a {
                    display: -webkit-box;
                    -webkit-line-clamp: 3;
                    overflow: hidden;
                    height: 66px;
                    text-align: left;
                    -webkit-box-orient: vertical;
                }
            }
            &-3 {
                @media(max-width: 767px) {
                    display: none;
                }
            }
            &-5 {
                @media(max-width: 767px) {
                    justify-content: flex-start;
                }
            }
        }
    }
    &-quantity {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        width: 100%;
        min-width: 80px;
        max-width: 80px;
        & > div {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
        }
        & button {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 25px;
            height: 25px;
            background: none;
            outline: none;
            transition: 0.3s ease;
            & svg {
                & .rect-1 {
                    fill: #EFEFEF;
                    transition: 0.3s ease;
                }
                & .rect-2,
                & path {
                    fill: #2756ED;
                    stroke: #2756ED;
                    transition: 0.3s ease;
                }
            }
            &:hover {
                & svg {
                    & .rect-1 {
                        fill: #2756ED;
                    }
                    & .rect-2,
                    & path {
                        fill: #fff;
                        stroke: #fff;
                    }
                }
            }
        }
        & input {
            background: none !important;
            outline: none;
            width: 33px;
            padding: 0 5px;
            text-align: center;
            font-family: 'Montserrat SemiBold';
            font-size: 18px;
            line-height: 18px;
            color: #303030;
        }
    }
}
